.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.clinic-selector {
  margin-bottom: 30px;
  text-align: center;
}

.clinic-selector h2 {
  color: #2c3e50;
  margin-bottom: 15px;
}

.clinic-select {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  background-color: white;
  cursor: pointer;
}

.main-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.buttons-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.button-row {
  display: grid;
  gap: 15px;
}

.button-row:first-child {
  grid-template-columns: repeat(3, 1fr);
}

.button-row:last-child {
  grid-template-columns: repeat(4, 1fr);
}

.results-container {
  background: white;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.results-container h2 {
  color: #2c3e50;
  margin: 0 0 15px 0;
  font-size: 1.2rem;
}

.manual-edit-warning {
  background: #fff3cd;
  color: #856404;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 15px;
  font-size: 0.9rem;
}

.doctor-notes {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.doctor-notes textarea {
  width: 100%;
  min-height: 200px;
  padding: 15px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  font-size: 0.9rem;
  line-height: 1.5;
  resize: vertical;
}

.copy-button {
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  align-self: flex-end;
}

.copy-button:hover {
  background: #45a049;
}

.copy-notification {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #4CAF50;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 0.9rem;
  animation: fadeInOut 2s ease-in-out;
  z-index: 1000;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  15% {
    opacity: 1;
    transform: translateY(0);
  }
  85% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-20px);
  }
}

@media (max-width: 768px) {
  .button-row:first-child,
  .button-row:last-child {
    grid-template-columns: 1fr;
  }
  
  .clinic-select {
    max-width: 100%;
  }
}