.button-editor {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin: 15px;
}

.button-editor h3 {
    color: #2c3e50;
    margin: 0 0 15px 0;
    font-size: 1.2rem;
    font-weight: 600;
}

.add-item-container {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
}

.add-item-container input {
    flex: 1;
    padding: 8px 12px;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    font-size: 0.9rem;
}

.add-item-container input:focus {
    outline: none;
    border-color: #4CAF50;
}

.add-button {
    padding: 8px 16px;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.add-button:hover {
    background: #45a049;
}

.items-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.item-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    background: #f8f9fa;
    border-radius: 8px;
    border: 1px solid #e9ecef;
}

.item-row span {
    color: #495057;
    font-size: 0.9rem;
}

.delete-button {
    padding: 4px 8px;
    background: #ff6b6b;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.8rem;
    transition: background-color 0.2s ease;
}

.delete-button:hover {
    background: #ff5252;
}

.error-message {
    background: #ff6b6b;
    color: white;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 15px;
    font-size: 0.9rem;
} 