.button-handler {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin: 15px;
    transition: all 0.3s ease;
}

.button-handler.disabled {
    opacity: 0.7;
    pointer-events: none;
    user-select: none;
}

.button-handler h3 {
    color: #2c3e50;
    margin: 0 0 15px 0;
    font-size: 1.2rem;
    font-weight: 600;
}

.button-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.button-container.disabled {
    pointer-events: none;
    user-select: none;
}

.button-wrapper {
    position: relative;
}

.button-wrapper.disabled {
    pointer-events: none;
    user-select: none;
}

button {
    background: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    padding: 8px 16px;
    font-size: 0.9rem;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s ease;
    min-width: 120px;
}

button:hover:not(.disabled) {
    background: #e9ecef;
    transform: translateY(-1px);
}

button.selected {
    background: #4CAF50;
    color: white;
    border-color: #4CAF50;
}

button.disabled {
    opacity: 0.5;
    cursor: not-allowed;
    background: #e9ecef;
    transform: none;
    pointer-events: none;
    user-select: none;
}

button.disabled:hover {
    transform: none;
    background: #e9ecef;
}

.usage-count {
    position: absolute;
    top: -8px;
    right: -8px;
    background: #ff6b6b;
    color: white;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.7rem;
    font-weight: bold;
}

.error-message {
    background: #ff6b6b;
    color: white;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 15px;
    font-size: 0.9rem;
} 