.settings-container {
    padding: 20px;
    background: #f8f9fa;
    border-radius: 8px;
    margin: 20px;
}

.settings-container h2 {
    color: #2c3e50;
    margin-bottom: 20px;
    font-size: 1.5rem;
}

.button-editors {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.button-row {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
} 